.first_screen {
  position: relative;
  height: 650px;
  padding: 30px;
}

.first_screen_overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.first_screen_video {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.first_screen_overlay {
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

.first_screen_content {
  position: relative;
  z-index: 2;
  color: #fff;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  justify-content: center;
}

.first_screen_title {
  font-size: 50px;
  margin-bottom: 15px;
}

.first_screen_subtitle {
  font-size: 16px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
}
